<template>
	<view>
		<view class="content">
			<uni-forms :modelValue="formData">
				<uni-forms-item label="姓名" name="name">
					<uni-easyinput type="text" v-model="formData.name" placeholder="请输入姓名" />
				</uni-forms-item>
				<uni-forms-item label="手机号" name="phone">
					<uni-easyinput type="text" v-model="formData.phone" placeholder="请输入手机号" />
				</uni-forms-item>
				<uni-forms-item label="密码" name="password">
					<uni-easyinput type="password" v-model="formData.password" placeholder="请输入密码" />
				</uni-forms-item>
				<uni-forms-item label="确认密码" name="repassword">
					<uni-easyinput type="password" v-model="formData.repassword" placeholder="请输入密码" />
				</uni-forms-item>
				<button @click="register">注册</button>
			</uni-forms>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				formData: {
					name: '',
					phone: '',
					password: '',
					repassword: ''
				}
			}
		},
		methods: {
			register() {
				if (this.formData.password !== this.formData.repassword) {
					uni.showToast({
					  title: '两次密码不一致，注册失败',
					  icon: 'none'
					})
				}
				uni.request({
					// #ifdef H5
					url: "api/save/user",
					// #endif
					// #ifdef MP-WEIXIN
					url: 'http://127.0.0.1:8080/api/save/user',
					// #endif
					method: 'POST',
					data: {
						name: this.formData.name,
						phone: this.formData.phone,
						password: this.formData.password
					},
					success: (res) => {
					  if (res.data.code === 200) {
						uni.redirectTo({
						  url: '/pages/index/index'
						})
						uni.showToast({
						  title: '注册成功'
						})
					  } else if (res.data.code === 400) {
						uni.showToast({
						  title: res.data.message,
						  icon: 'none'
						 })
					  } else {
						uni.showToast({
						  title: '注册失败',
						  icon: 'none'
						})
					  }
					},
					fail: (err) => {
					  console.log(err)
					  uni.showToast({
						title: '网络错误',
						icon: 'none'
					  })
					}
				})
			}
		}
	}
</script>


<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
	
	.uni-form-item .title {
		padding: 20rpx 0;
	}
</style>


